.order {
  display: flex;
  justify-content: flex-end;
  padding-bottom: 50px;
}

.order__container {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding-top: 52px;
  width: 100%;
}

.order__body {
  margin-left: 185px;
  padding-left: 7vw;
}

.order__title {
  max-width: 550px;
  font-weight: 500;
  font-size: 50px;
  line-height: 60px;
  color: var(--color-black);
}

.order__dsr {
  margin-top: 15px;
  max-width: 900px;
  font-weight: 400;
  font-size: 32px;
  line-height: 40px;
  color: var(--color-black);
}

.form {
  padding-top: 40px;
  padding-bottom: 25px;
  max-width: 760px;
}

.form__item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.form__item:nth-child(4) {
  margin-bottom: 14px;
}

.form__item:nth-child(6) {
  margin-bottom: 23px;
}

.form__btn {
  padding: 0;
  background-color: transparent;
  border: none;
  margin-top: 15px;
  width: 288px;
  height: 64px;
  color: var(--color-white);
  background-color: var(--color-blue);
  transition: background-color 0.3s ease-in-out;
}

.form__btn:hover {
  background-color: #ff4664;
  transition: background-color 0.3s ease-in-out;
}

.form__consent {
  margin-top: 20px;
  width: 280px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--color-black);
}

.form-title {
  font-weight: 400;
  font-size: 32px;
  color: var(--color-gray);
}

.form-input {
  border: 1px solid var(--color-gray);
  width: 600px;
  height: 36px;
}

.textarea {
  overflow: hidden;
  overflow-wrap: break-word;
  border: 1px solid var(--color-gray);
  width: 600px;
  resize: none;
}

.project__items {
  display: flex;
  justify-content: space-between;
  padding-bottom: 8px;
  width: 600px;
}

.project__input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.project > span {
  display: flex;
  align-items: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.project__item::before {
  content: "";
  display: inline-block;
  border: 1px solid var(--color-gray);
  padding: 5px 10px;
  font-weight: 400;
  font-size: 16px;
  text-align: center;
  color: var(--color-gray);
  cursor: pointer;
}

.project__item--logo::before {
  content: "логотип";
}

.project__item--identity::before {
  content: "фирменный стиль";
}

.project__item--site::before {
  content: "сайт-визитка";
}

.project__item--landing::before {
  content: "лендинг";
}

.project__item--other::before {
  content: "другое";
}

.project__label > input:checked + span::before {
  border-color: var(--color-blue);
  color: var(--color-blue);
}

.task__title {
  max-width: 120px;
  line-height: 40px;
}

.budget {
  display: flex;
  justify-content: flex-start;
}

.budget__title {
  width: 160px;
}

.budget__items {
  display: flex;
  justify-content: space-between;
  padding-bottom: 8px;
  width: 465px;
}

.budget__input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.budget__input > span {
  display: flex;
  align-items: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.budget__item::before {
  content: "";
  display: inline-block;
  border: 1px solid var(--color-gray);
  padding: 5px 10px;
  font-weight: 400;
  font-size: 16px;
  text-align: center;
  color: var(--color-gray);
  cursor: pointer;
}

.budget__item--min::before {
  content: "до 50 000₽";
}

.budget__item--mid::before {
  content: "до 100 000₽";
}

.budget__item--max::before {
  content: "до 300 000₽";
}

.budget__item--other::before {
  content: "п*хуй";
}

.budget__label > input:checked + span::before {
  border-color: var(--color-blue);
  color: var(--color-blue);
}

input::-webkit-input-placeholder {
  padding-left: 10px;
  color: var(--color-gray);
}

input::-moz-placeholder {
  padding-left: 10px;
  color: var(--color-gray);
}

.customer-phone__input::-webkit-input-placeholder {
  color: var(--color-gray);
  text-indent: 0px;
  -webkit-transition: text-indent 0.3s ease;
  transition: text-indent 0.3s ease;
}

.customer-phone__input::-moz-placeholder {
  color: var(--color-gray);
  text-indent: 0px;
  -moz-transition: text-indent 0.3s ease;
  transition: text-indent 0.3s ease;
}

.customer-phone__input:-moz-placeholder {
  color: var(--color-gray);
  text-indent: 0px;
  -moz-transition: text-indent 0.3s ease;
  transition: text-indent 0.3s ease;
}

.customer-phone__input:-ms-input-placeholder {
  color: var(--color-gray);
  text-indent: 0px;
  -ms-transition: text-indent 0.3s ease;
  transition: text-indent 0.3s ease;
}

.customer-phone__input:focus::-webkit-input-placeholder {
  text-indent: 600px;
  -webkit-transition: text-indent 0.3s ease;
  transition: text-indent 0.3s ease;
}

.customer-phone__input:focus::-moz-placeholder {
  text-indent: 600px;
  -moz-transition: text-indent 0.3s ease;
  transition: text-indent 0.3s ease;
}

.customer-phone__input:focus:-moz-placeholder {
  text-indent: 600px;
  -moz-transition: text-indent 0.3s ease;
  transition: text-indent 0.3s ease;
}

.customer-phone__input:focus:-ms-input-placeholder {
  text-indent: 600px;
  -ms-transition: text-indent 0.3s ease;
  transition: text-indent 0.3s ease;
}

.works {
  padding-top: 190px;
}

.works__container {
  padding: 0;
}

.works__dsr {
  margin-left: 17.7vw;
  max-width: 900px;
  font-weight: 400;
  font-size: 32px;
  line-height: 40px;
  color: var(--color-black);
}

.works__body {
  display: flex;
  flex-wrap: wrap;
  margin-top: 50px;
}

.works__contacts {
  display: flex;
  flex-direction: column;
}

.works__order-link {
  font-weight: 700;
  font-size: 7.8125vw;
  letter-spacing: -0.02em;
}

.works__phone-number {
  font-weight: 500;
  font-size: 50px;
  color: var(--color-black);
}

.works__email {
  font-weight: 500;
  font-size: 50px;
  color: var(--color-black);
}

.work {
  overflow: hidden;
}

.work__title {
  position: absolute;
